:root {

  --orange-soda: #f06543ff;
  --platinum: #e8e9ebff;
  --alabaster: #e0dfd5ff;
  --onyx: #313638ff;
  --sandy-brown: #f09d51ff;

}

#colors() {
  primary: #f06543;
  secondary: #313638;
  accent1: #e8e9eb;
  accent2: #f09d51;
  base1: #e8e9eb;
}

@import 'icons.less';


body {
  overflow: hidden;
  margin: 0;
  padding: 0;
  background-color: #colors[base1];
  font-family: Helvetica, sans-serif;
  color: #colors[secondary];
  font-size: 10pt;
}


#appContainer {
  height:100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;

  .navbar {
    flex-basis: auto;
    flex-grow: 0;
    padding-left: 1rem;
    // padding: 1rem;
    // margin: 2rem;
    .title {
      font-size: 1.3rem;
      display: inline-block;
      font-weight: bold;
      height: 100%;
      padding-right: 1rem;
    }
    .nav-bar-item {
      display: inline-block;
      box-sizing: border-box;
      a {
        cursor: pointer;
        display: inline-block;
        box-sizing: border-box;
        padding: 0.5rem;
        height: 100%;
        &:hover {
          background-color: #colors[primary];
          color: #colors[secondary];
        }
      }
    }
    .material-icons {
      vertical-align: top;
    }
  }

  #appPrimary {
    flex-basis: 0;
    display: flex;
    flex-direction: row;
    height: 10rem;
    flex-grow: 100;

    #inputContainer {

      .editorContainer {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;

        &.hidden {
          display: none;
        }

        .editor {
            // width: 100%;
            // height: 100%;
            position: relative;
            flex-basis: 100%;
        }
      }
    };

    #outputContainer {
      display: flex;
      flex-direction: column;


      #renderOutputContainer {
        overflow: auto;
        iframe {
          height: 100%;
          width: 100%;
        }
      };
      #errorsOutputContainer {
        overflow:auto;

        .output-log {
          padding-left: 4px;
          padding-right: 4px;
          font-size: 0.8em;
          font-family: monospace;
          .log-entry {
            padding-left: 16px;
            padding-top: 3px;
            padding-bottom: 3px;
            border-bottom: 1px solid #colors[secondary];
            &.LOG {

            }
            &.ERROR {
              color: red;
            }
            &.DEBUG {

            }
          }
        }
      };
    };
  };
}

.gutter {
  background-color: #ddd;
  cursor: row-resize;
}

.gutter.gutter-horizontal {
  cursor: col-resize;
}

.gutter.gutter-vertical {
  cursor: row-resize;
}


.modal {
 display: none;
 position: fixed;
 visibility: hidden;
 z-index: 1;
 left: 0;
 top: 0;
 width: 100%;
 height: 100%;
 overflow: auto;
 background-color: #colors[base1];
 background-color: fade(#colors[base1],50%);

 &.active {
   display: block;
   visibility: visible;
 }

 .modal-content {
  background-color: #colors[accent1];
  margin: 30px auto;
  padding: 5px;
  border: 1px solid #888;
  width: 80%;

  .modal-header {
    padding: 2px 16px;
    background-color: #colors[secondary];
    color: #colors[primary];

    display: flex;

    h1 {
      flex-basis: 100%;
    }

    .modal-close {
     font-size: 28px;
     font-weight: bold;
     i {
       color: #colors[primary];
     }
    }
  }

  .modal-body {
    padding: 2px 16px;
    color: #colors[secondary];
  }

  .modal-footer {
    padding: 2px 16px;
    background-color: #colors[secondary];
    color: #colors[primary];
  }


 }
}
